/* FONTS */
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
/* COLOR */
/*Global reset*/
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  margin-top: 0 !important;
}

p {
  font-size: 1.6rem;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 1.7;
  box-sizing: border-box;
}

.desktop-nav {
  display: none;
}

#menu-huvud-menu .menu-item-12,
#menu-huvud-menu .menu-item-13 {
  float: left;
  margin-right: 13px;
}

.overlay-menu_logo img {
  width: 110px;
  margin-bottom: 30px;
}

.container {
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  text-align: center;
}
.container p {
  font-size: 2rem;
}
.container a {
  font-size: 2rem;
  position: relative;
  top: 5px;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  color: #4b4d4c;
}
.container a:after {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  height: 2px;
  content: "";
  transition: 0.35s ease;
  transform: translateX(-50%);
  background: #4b4d4c;
}
.container a:hover:after, .container a:focus:after, .container a:active:after {
  width: 100%;
}

h1 {
  position: relative;
  text-align: center;
}

.button_container {
  position: fixed;
  z-index: 100;
  top: 6%;
  right: 3%;
  width: 35px;
  height: 27px;
  cursor: pointer;
  transition: opacity 0.25s ease;
}
@media (min-width: 768px) {
  .button_container {
    display: none;
  }
}
.button_container:hover {
  opacity: 0.7;
}
.button_container.active .top {
  transform: translateY(11px) translateX(0) rotate(45deg);
  background: #FFF;
}
.button_container.active .middle {
  opacity: 0;
  background: #FFF;
}
.button_container.active .bottom {
  transform: translateY(-11px) translateX(0) rotate(-45deg);
  background: #FFF;
}
.button_container span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  cursor: pointer;
  transition: all 0.35s ease;
  border: none;
  background: #FFF;
}
.button_container span:nth-of-type(2) {
  top: 11px;
}
.button_container span:nth-of-type(3) {
  top: 22px;
}

.overlay {
  position: fixed;
  z-index: 50;
  top: 0;
  left: 0;
  visibility: hidden;
  overflow: hidden;
  width: 100%;
  height: 0%;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  opacity: 0;
  background: #4b4d4c;
}
.overlay.open {
  visibility: visible;
  height: 100%;
  opacity: 0.9;
}
.overlay.open li {
  animation: fadeInRight 0.5s ease forwards;
  animation-delay: 0.35s;
}
.overlay.open li:nth-of-type(2) {
  animation-delay: 0.4s;
}
.overlay.open li:nth-of-type(3) {
  animation-delay: 0.45s;
}
.overlay.open li:nth-of-type(4) {
  animation-delay: 0.5s;
}
.overlay nav {
  font-family: "Varela Round", serif;
  font-size: 50px;
  font-weight: 400;
  position: relative;
  top: 50%;
  height: 70%;
  transform: translateY(-50%);
  text-align: center;
}
.overlay ul {
  position: relative;
  display: inline-block;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}
.overlay ul li {
  position: relative;
  display: block;
  height: 25%;
  height: 25%;
  min-height: 50px;
  opacity: 0;
}
.overlay ul li a {
  font-size: 2.8rem;
  position: relative;
  display: block;
  overflow: hidden;
  text-decoration: none;
  color: #FFF;
}
.overlay ul li a:hover:after, .overlay ul li a:focus:after, .overlay ul li a:active:after {
  width: 100%;
}
.overlay ul li a:after {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  height: 3px;
  content: "";
  transition: 0.35s;
  transform: translateX(-50%);
  background: #FFF;
}

@keyframes fadeInRight {
  0% {
    left: 20%;
    opacity: 0;
  }
  100% {
    left: 0;
    opacity: 1;
  }
}
@media (min-width: 768px) {
  .desktop-nav {
    padding: 20px;
    background-color: #242323;
    justify-content: space-around;
    align-items: center;
    display: -webkit-box;
    /* OLD - iOS 6-, Safari 3.1-6 */
    display: -moz-box;
    /* OLD - Firefox 19- (buggy but mostly works) */
    display: -ms-flexbox;
    /* TWEENER - IE 10 */
    display: -webkit-flex;
    /* NEW - Chrome */
    display: flex;
  }
  .desktop-nav_logo img {
    width: 110px;
  }

  ul#menu-huvud-menu-1 {
    justify-content: flex-end;
    align-items: baseline;
    display: -webkit-box;
    /* OLD - iOS 6-, Safari 3.1-6 */
    display: -moz-box;
    /* OLD - Firefox 19- (buggy but mostly works) */
    display: -ms-flexbox;
    /* TWEENER - IE 10 */
    display: -webkit-flex;
    /* NEW - Chrome */
    display: flex;
  }
  ul#menu-huvud-menu-1 li {
    margin-right: 10px;
    list-style: none;
  }
  ul#menu-huvud-menu-1 li a {
    font-size: 1.6rem;
    color: white;
  }
}
main {
  position: relative;
  width: 100%;
  height: 90vh;
}

.header-content {
  width: 100%;
  height: 200px;
  color: white;
  background-color: #363636;
}

.header-text-container {
  padding: 3rem;
  text-align: center;
}

.container-fluid {
  padding-right: 0px;
  padding-left: 0px;
}

.fontawesome-link a {
  font-size: 2rem;
  margin-left: 10px;
  color: red;
}

.site-header__avatar img {
  width: 23%;
  border-radius: 50%;
  margin-right: 10px;
  height: auto;
}

/* BUTTONS */
.btn {
  display: inline-block;
  cursor: pointer;
  border-radius: 4px;
  overflow: hidden;
  text-decoration: none;
  color: #FFF;
  font-size: 1.19rem;
  padding: 12px 24px;
  border: none;
  outline: none;
}
.btn--small {
  font-size: 1.3rem;
  padding: 7px 13px;
  font-weight: 300;
}
.btn--with-photo {
  padding-left: 40px;
  position: relative;
}
.btn--orange {
  background-color: #cf7e15;
}
.btn--orange:hover {
  background: linear-gradient(#cf7e15, color(#cf7e15 b(30%) s(80%)));
}
.btn--dark-orange {
  background-color: #ff7e06;
}
.btn--dark-orange:hover {
  background: linear-gradient(#ff7e06, color(#ff7e06 b(40%) s(80%)));
}
.btn--blue {
  background-color: #1902eb;
}
.btn--blue:hover {
  background: linear-gradient(color(#1902eb b(-16%)), #1902eb);
}
.btn--yellow {
  background-color: #f3f708;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.22);
}
.btn--yellow:hover {
  background: linear-gradient(#f3f708, color(#f3f708 b(40%) s(80%)));
}
.btn--beige {
  background-color: #eaebc2;
  color: #173f58;
}
.btn--beige:hover {
  background-color: #f3f708;
}
.btn--gray {
  background-color: #222;
}
.btn--white {
  background-color: #FFF;
  color: #173f58;
}
.btn--white:hover {
  background-color: #DDD;
}
.btn--large {
  font-size: 1.3rem;
  padding: 16px 34px;
  border-radius: 7px;
}
.btn--inactive {
  background-color: transparent;
  cursor: default;
  color: #333;
}

.footer-content {
  position: relative;
  width: 100%;
  height: auto;
  color: white;
  background-color: #363636;
}

.footer-text-container {
  padding: 20px;
  text-align: center;
}

.temporary-main {
  margin: 30px auto;
  width: 80%;
}

.frontpage-group .wp-block-group__inner-container {
  width: 80%;
  margin: 0 auto;
  padding: 20px;
}

.content-block {
  width: 100%;
  margin: 0 auto;
  display: flex;
  padding: 20px;
}

.left-block {
  width: 50%;
  border: 1px solid darkgray;
}

.right-block {
  width: 50%;
}

.card-book {
  color: white;
  display: -webkit-box;
  /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box;
  /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox;
  /* TWEENER - IE 10 */
  display: -webkit-flex;
  /* NEW - Chrome */
  display: flex;
  width: 100%;
  height: auto;
  margin: 0 auto;
  margin: 10px;
}
.card-book .image-block .image {
  background-color: tomato;
  height: 100%;
  width: 200px;
}
.card-book .info-block {
  background-color: indigo;
  padding: 10px 20px;
  width: 75%;
}
.card-book .info-block small {
  display: block;
}

.price {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}